<template>
  <div class="container">
    <div class="data">
      <div class="table">
        <base-table
          :columns="columns"
          :data="data"
          row-key="type"
        @downloadModel="downloadModel"
        @importData="importData"
        @exportData="exportData">
        </base-table>
      </div>
    </div>
  </div>
</template>

<script>
const BaseTable = () => import('@/components/BaseTable')
export default {
  name: 'Rule',
  components: {BaseTable},
  data () {
    return {
      columns: [
        {prop: 'type', width: 120, label: '检视类型', align: 'center'},
        {
          prop: 'operation',
          label: '操作',
          width: 360,
          operation: true,
          operations: [
            {text: '模板下载', event: 'downloadModel'},
            {text: '数据导入', event: 'importData'},
            {text: '数据导出', event: 'exportData'}
          ]
        }
      ],
      data: [
        {type: '预算对比-配费模式'},
        {type: '预算对比-分机构'},
        {type: '预算对比-分业务板块'},
        {type: '定价策略预测对比-个车'},
        {type: '定价策略预测对比-协议'}
      ]
    }
  },
  methods: {
    downloadModel () {},
    importData () {},
    exportData () {}
  }
}
</script>

<style scoped>

</style>



